/* ----------------------------------------
   ----------- 1.GENERAL STYLES -----------
   ---------------------------------------- */


/* Google font - Defualt Fonts*/

/* Google Font for Home Page Two */

/* change here, full html color,size,background will same as here */

:root {
    --main-color: #fc5185;
    --second-color: #004caa;
    --heading-color: #222222;
    --body-color: #666;
    --section-bg: #004caa;
    --box-shadow: 8.135px 18.271px 40px 0px rgba(0, 0, 0, 0.08);
    --heading-family: 'Josefin Sans', sans-serif;
    --body-family: 'Roboto Slab', serif;
    --main-color-two: #FF8A8B;
    --second-color-two: #1E266D;
    --heading-color-two: #222222;
    --body-color-two: #666666;
    --section-bg-two: #1E266D;
    --box-shadow-two: 10px 15px 40px rgba(0, 0, 0, 0.06);
    --heading-family-two: 'Poppins', sans-serif;
    --body-family-two: 'Lora', serif;
}

* {
    margin: 0px;
    padding: 0px;
}

body,
html,
.wrapper {
    height: 100%;
}

body {
    font-family: 'Roboto Slab', serif !important;
    font-family: var(--body-family) !important;
    color: #666 !important;
    color: var(--body-color) !important;
    font-size: 16px !important;
    line-height: 30px !important;
    font-weight: 400 !important;
    overflow-x: hidden !important;
}

body.style-two {
    font-family: 'Lora', serif !important;
    font-family: var(--body-family-two) !important;
    color: #666666 !important;
    color: var(--body-color-two) !important;
    font-size: 18px;
    line-height: 28px;
    font-weight: 400;
    overflow-x: hidden;
}

a:focus {
    outline: 0 solid
}

a {
    cursor: pointer;
}

img {
    max-width: 100%;
    height: auto;
}

input::-moz-focus-inner {
    border: 0;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus {
    -webkit-box-shadow: 0 0 0px 1000px white inset !important;
}

ul {
    padding: 0;
    margin: 0 !important;
    list-style-type: none;
}

a:hover {
    text-decoration: none;
    outline: none !important;
}

a:focus,
a:active {
    text-decoration: none;
    outline: none !important;
}

:-ms-input-placeholder {
    color: #666;
    opacity: 1;
    /* Firefox */
}

::placeholder {
    color: #666;
    opacity: 1;
    /* Firefox */
}

:-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: #666;
}

::-ms-input-placeholder {
    /* Microsoft Edge */
    color: #666;
}


/* section padding */

.section-padding {
    padding: 120px 0px;
}

.section-bg {
    background: #004caa;
    background: var(--second-color);
}

.section-bg-two {
    background: #1E266D;
    background: var(--second-color-two);
}

.section-title-one {
    margin-bottom: 27px;
}

.section-title-one h1 {
    line-height: 1;
}

.user-form-one {
    width: 50%;
    margin-bottom: 50px;
}

.theme-bg {
    background: #fc5185;
    background: var(--main-color);
}

.theme-gradient {
    background: #fc5185;
    background: var(--main-color);
    background: linear-gradient(to left, #fc5185, #fc5185);
    background: linear-gradient(to left, var(--main-color), var(--main-color));
}

.theme-btn:hover {
    background: #fff;
    color: #000 !important;
    border-color: #fff;
}

.theme-btn {
    display: inline-block;
    border-width: 1px;
    border-color: #fc5185;
    border-color: var(--main-color);
    border-style: solid;
    height: 61px;
    text-align: center;
    line-height: 55px !important;
    padding: 0px 55px;
    background: #fc5185;
    background: var(--main-color);
    color: #fff;
    /* text-transform: capitalize; */
    transition: .4s;
    position: relative;
    z-index: 2;
    border-radius: 3px;
}

.theme-btn.btn_black {
    border-color: #fff;
    color: #fff;
    background: transparent;
    transition: .4s;
}

.theme-btn.btn_black:hover {
    border-color: #fc5185;
    border-color: var(--main-color);
    color: #fff !important;
    background: #fc5185;
    background: var(--main-color);
}

.theme-btn.fill-btn {
    margin-right: 20px;
}

.text-white .btn_black:hover {
    color: #fff !important;
}

.btn-link {
    color: #fc5185;
    color: var(--main-color);
    font-size: 16px;
    line-height: 24px;
    /* text-transform: capitalize; */
    font-weight: 700;
    padding-bottom: 4px;
    border-bottom: 2px solid #fc5185;
    border-bottom: 2px solid var(--main-color);
    transition: .4s;
    display: inline-block;
}

.btn-link:hover {
    text-decoration: none;
}


/* ----------------------------------------
   -------------- 2.TYPOGRAPHY ------------
   ---------------------------------------- */

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0px;
    font-weight: 600 !important;
    color: #222222;
    color: var(--heading-color);
    font-family: 'Josefin Sans', sans-serif;
    font-family: var(--heading-family);
}

h1 {
    font-size: 46px;
    line-height: 60px;
}

h2 {
    font-size: 36px;
    line-height: 48px;
}

h3 {
    font-size: 24px;
    line-height: 36px;
}

h4 {
    font-size: 20px;
    line-height: 30px;
}

h5 {
    font-size: 18px;
    line-height: 26px;
}

h6 {
    font-size: 14px;
    line-height: 24px;
}

p {
    margin-bottom: 0px;
}

a {
    text-decoration: none;
    font-weight: 700;
    outline: none !important;
    cursor: pointer;
}


/* Style Two TYPOGRAPHY 
------------------------------*/

body.style-two h1,
body.style-two h2,
body.style-two h3,
body.style-two h4,
body.style-two h5,
body.style-two h6 {
    font-family: 'Poppins', sans-serif;
    font-family: var(--heading-family-two);
    margin: 0px;
    font-weight: 600;
    color: #222222;
    color: var(--heading-color-two);
}

body.style-two>a {
    font-weight: 400;
    font-size: 16px;
    line-height: 25px;
    font-size: 'Poppins', sans-serif !important;
    font-size: var(--heading-family-two) !important;
}


/* preloader */

.pre-loader {
    position: fixed;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background: #9AE7FC;
    z-index: 9999999999999999999;
    left: 0;
    top: 0;
}

.loader {
    width: 300px;
    height: 250px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.double-bounce1,
.double-bounce2 {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #fff;
    opacity: 0.6;
    position: absolute;
    top: 0;
    left: 0;
    animation: sk-bounce 2.0s infinite ease-in-out;
}

.double-bounce2 {
    animation-delay: -1.0s;
}

@keyframes sk-bounce {
    0%,
    100% {
        transform: scale(0.0);
        -webkit-transform: scale(0.0);
    }
    50% {
        transform: scale(1.0);
        -webkit-transform: scale(1.0);
    }
}


/* scrollup */

#scrollUp {
    background-color: #fc5185;
    background-color: var(--main-color);
    border-radius: 50%;
    bottom: 20px;
    color: #ffffff;
    font-size: 22px;
    height: 50px;
    line-height: 50px;
    right: 20px;
    text-align: center;
    width: 50px;
    box-shadow: 0 10px 20px 0 rgba(23, 22, 49, 0.1);
    font-weight: 400;
}


/* header */

.main-menu {
    position: relative;
    transition: all .5s ease-in-out;
}

.main-menu nav li a {
    color: #000066;
    /* text-transform: capitalize; */
    padding-left: 40px;
    font-size: 18px;
    font-weight: 500;
    text-decoration: none;
}

.main-menu ul>li a:hover {
    color: #fc5185;
    color: var(--main-color);
}

.main-menu ul li ul li:hover a {
    background: #fff;
    color: #000066;
}

.menu-style-one ul li ul {
    position: absolute;
    width: 190px;
    text-align: center;
    background: #fc5185;
    background: var(--main-color);
    z-index: 9999;
    left: 0;
    top: 100%;
    opacity: 0;
    visibility: hidden;
    transition: all .5s;
    transform: translateY(25px);
    box-shadow: 0px 13px 35px -12px rgba(35, 35, 35, 0.15);
}

.menu-style-one ul li {
    display: inline-block;
    position: relative;
}

.menu-style-one ul li ul li {
    display: block;
}

.menu-style-one ul li ul li a {
    padding-left: 0px;
    display: block;
    color: #fff;
    padding: 4px 0px;
}

.menu-style-one ul li:hover ul {
    visibility: visible;
    opacity: 1;
    transform: translateY(0px);
}

.navbar.navbar-expand-md {
    padding: 0;
}

.sticky {
    top: 0;
    width: 100%;
    position: fixed !important;
    padding: 0px;
    background: #fff;
    animation: sticky .9s ease-in-out;
    box-shadow: 0 13px 35px -12px rgba(35, 35, 35, .15);
}

.header-one.sticky {
    transform: translateY(0px);
}

.header-two.sticky {
    padding: 5px 0px;
}

@media (max-width: 767px) {
    position: relative;
}

.header-one {
    position: absolute;
    width: 100%;
    z-index: 99999;
    transform: translateY(50px);
}

.main-menu .container,
.new-container {
    background: #fff;
    border-radius: 10px;
    transition: all .5s ease-in-out;
    padding: 0px 30px;
}

.map-container {
    display: flex;
}

.map-section {
    height: 800px;
    margin: auto;
    width: 100%;
}

.circle {
    background: #d83b01;
    border-radius: 50%;
    color: #fff;
    height: 3em;
    position: relative;
    width: 3em;
    border: 1px solid transparent;
}

.circleText {
    text-align: center;
    font-size: 17pt;
    height: 100%;
    left: 50%;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 70%;
}

.main-menu nav li a {
    padding-top: 25px;
    padding-bottom: 25px;
    display: inline-block;
    transition: .4s;
}


/* main menu two - 2 style */

.main-menu-two .menu-items li {
    display: inline-block;
    position: relative;
    margin-right: 40px;
}

.main-menu-two .menu-items li .submenu {
    position: absolute;
    left: 0;
    background: #FF8A8B;
    min-width: 200px;
    top: 0;
    transform: translateY(100px);
    transition: 1s;
    opacity: 0;
    visibility: hidden;
    z-index: 99999;
}

.main-menu-two .menu-items li a {
    /* text-transform: capitalize; */
    padding: 20px 0px;
    font-size: 18px;
    display: inline-block;
    color: #fff;
    font-weight: 400;
    position: relative;
    transition: .4s;
}

.header-two {
    padding: 31px 0px;
    background: #1E266D;
    background: var(--second-color-two);
    display: block;
    width: 100%;
    z-index: 9999;
    position: relative;
    transition: .5s;
}

.home-slider-two p {
    color: #555;
}

.main-menu-two .menu-items li:last-child {
    margin-right: 0px;
}

.menu-items li .submenu li {
    margin-right: 0;
    display: block;
    text-align: center;
}

.main-menu-two .menu-items li .submenu li a {
    padding: 8px;
    border: none;
    border-bottom: 1px solid #fff3f496;
    display: block;
    box-sizing: border-box;
    transition: .4s;
}

.main-menu-two .menu-items li:hover .submenu {
    opacity: 1;
    visibility: visible;
    transform: translateY(70px);
    z-index: 999999;
}

.main-menu-two .menu-items li .submenu li:last-child a {
    border-bottom: 0;
}

.main-menu-two .menu-items>li>a::before {
    position: absolute;
    left: 0;
    width: 0;
    height: 2px;
    content: "";
    background: #fc5185;
    background: var(--main-color);
    top: 0;
    transition: .5s;
}

.main-menu-two .menu-items>li:hover a::before {
    width: 100%;
}


/* hero slider home one */

.single-hero-slide {
    background: #004caa;
    background: var(--second-color);
    padding-top: 340px;
    padding-bottom: 220px;
    position: relative;
}

@media (min-width: 991px) {
    .hero-slide-left {
        padding-right: 60px;
    }
}

.hero-slide-left p {
    color: #ddd !important;
}

.hero-slide-left h1 {
    font-size: 62px;
    line-height: 75px;
    margin-bottom: 20px;
}

.hero-slide-left h2 {
    font-size: 36px;
}

.hero-slide-left a {
    margin-top: 40px;
}

.hero-slide-right {
    position: relative;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: bottom;
    display: flex;
    align-items: flex-end;
}

.animate-img {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 22;
}

.animate-img img:first-child {
    left: -91px;
    position: absolute;
    animation-name: movebounce;
    animation-delay: 100ms;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
    animation-duration: 2s;
    animation-direction: alternate;
    bottom: 40px;
}

.animate-img .aimg2 {
    margin-left: 88px;
    animation-name: movebounce;
    animation-delay: 200ms;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
    animation-duration: 2.5s;
    animation-direction: alternate;
    bottom: 30px;
}


/* features section */

.icon-box {
    display: flex;
    background-image: url(data:image/png;base64,);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: bottom;
    width: 103px;
    height: 68px;
    align-items: flex-end;
    margin: 0px auto;
    text-align: center;
    margin-bottom: 45px;
}

.single-feature-box h3 {
    margin-bottom: 10px;
}

.single-feature-box {
    text-align: center;
}

.icon-box img {
    display: inline-block;
    margin: 0px auto;
}

.single-feature-box {
    border-radius: 10px;
    background-color: rgb(255, 255, 255);
    box-shadow: 8.135px 18.271px 40px 0px rgba(0, 0, 0, 0.08);
    padding: 60px 50px 75px;
    transition: all .4s;
}

.single-feature-box:hover {
