}

.single-funfact-one p {
    margin-top: 8px;
}

.single-service-box p,
.single-service-box a {
    color: #d7d3d3 !important
}

.single-funfact-one span::before {
    position: absolute;
    content: "+";
    left: 105%;
}

.single-funfact-one p {
    color: #666;
    font-weight: 300;
    line-height: 1;
}

.single-funfact-one {
    position: relative;
    width: 50%;
    margin-bottom: 45px;
    float: left;
}

.fun-fact-section .row {
    margin-bottom: -45px;
    margin-left: 0;
    margin-right: 0;
}


/* service section
-------------------------------  */

.single-service-box {
    border-width: 1px;
    border-color: #3672bc;
    border-style: solid;
    border-radius: 5px;
    padding: 60px 47px 70px;
    margin-top: 30px;
    transition: .5s;
    background: transparent;
}

.service-icon {
    margin-bottom: 44px;
}

.single-service-box h3 {
    margin-bottom: 12px;
}

.single-service-box a {
    margin-top: 18px;
}

.single-service-box:hover {
    background: #1259b0;
    cursor: cell;
    border-color: transparent;
}

.single-service-box:hover a {
    color: #fc5185 !important;
    color: var(--main-color) !important;
}


/* Gallery 
-------------------- */

.single-gallery-item {
    border-radius: 12px;
    background-color: #eee;
    height: 332px;
    z-index: 1;
    margin-top: 30px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    overflow: hidden;
}

.single-gallery-item img {
    max-width: 100% !important;
}

.single-gallery-item a {
    color: #fff;
}

.gallery-over {
    position: absolute;
    width: 100%;
    left: 0;
    height: 100%;
    top: 0;
    text-align: center;
    content: "";
}

.gallery-over::before {
    position: absolute;
    width: 100%;
    left: 0;
    height: 100%;
    top: 0;
    text-align: center;
    content: "";
    background: #fc5185;
    background: var(--main-color);
    transition: .5s;
    opacity: 0;
    visibility: hidden;
    transform: scale(0);
    z-index: -1;
    border-radius: 12px;
}

.gallery-over a {
    position: absolute;
    transform: translate(-50%, -50%);
    left: 50%;
    font-size: 50px;
    top: 50%;
    width: 50px;
    height: 50px;
    border-radius: 50px;
    transition: .5s;
    opacity: 0;
    visibility: hidden;
    z-index: 1;
}

.single-gallery-item:hover .gallery-over::before {
    visibility: visible;
    opacity: .8;
    transform: scale(1);
    -moz-transform: scale(1);
    -o-transform: scale(1);
}

.single-gallery-item:hover .gallery-over a {
    opacity: 1;
    visibility: visible;
}


