}

.promo-right-banner {
    margin-top: 32px;
    position: relative;
    z-index: 1;
}


/* doctor team
--------------------- */

.single-doctor {
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 8.135px 18.271px 40px 0px rgba(0, 0, 0, 0.08);
    padding: 50px 56px;
    margin-top: 30px;
    transition: .4s;
}

.doctor-profile {
    border-radius: 50%;
